<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
html,body{
      margin: 0;
      padding: 0;
      font-family: 'Roboto', sans-serif;
    }
    .title{
      color: #5e47a9;
      padding-left: 10px;
      font-size: 22px;
      margin-bottom: 20px;
    }
    .itens-container{
      width: 100%;
      height: 200px;
      margin-bottom: 20px;
      overflow-y: hidden;
      overflow-x: scroll;
      -webkit-overflow-scrolling: touch;
    }
    .item .image img{
      width: 90px;
      border-radius: 100%;
      height: 90px;
    }
    .item .image{
      width: 90px;
      margin: auto;
    }
    .item{
      width: 165px;
      float: left;
      height: 200px;
      position: relative;
      padding: 0px 10px;
    }
    .item::after{
      height: calc(100% - 20px);
      content: " ";
      position: absolute;
      right: 0px;
      background: #f1f1f1;
      width: 1px;
      top: 10px;
    }
    .item .name{
      margin-bottom: 5px;
      font-size: 18px;
      height: 32px;
      display: -webkit-box;
      line-height: 16px;     /* fallback */
      max-height: 32px;      /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      overflow:hidden;
      text-overflow: ellipsis;
    }
    .item .price{
      font-size: 18px;
      margin-top: 7px;
      margin-bottom: 7px;
      color: #36a982;
    }

    .item .name,.item .description,.item .price{
      text-align: center;
    }
    .item .description{
      color: #7e7e7e;
      display: -webkit-box;
      line-height: 16px;     /* fallback */
      max-height: 32px;      /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      overflow:hidden;
      text-overflow: ellipsis;
    }
    .itens-wrapper{
      /*width: calc((165px + 10px) * 4 - (10px * 4));*/
      float: left;
      height: 215px;
    }
    .itens-wrapper .item:last-child::after{
      display: none;
    }
    .featured{
      width: 100%;
      height: 150px;
      margin-top: 20px;
      margin-bottom: 20px;
      overflow-y: hidden;
      overflow-x: scroll;
      -webkit-overflow-scrolling: touch;
    }
