import axios from 'axios'
export default {
  get: async ({commit}) => {
    // const { data: features } = process.env.NODE_ENV === 'development' ? await axios.get('https://api.myjson.com/bins/eu1wt') : await axios.get('/Home/ObterFeatures')
    // const { data: features } = process.env.NODE_ENV === 'development' ? await axios.get('https://api.myjson.com/bins/ihdot') : await axios.get('/Home/ObterFeatures')
    const { data: features } = await axios.get('https://api.myjson.com/bins/dn8hf')
    commit('SET_FEATURES', features)
    return true
  },
  goToAnalitics ({commit}, payload) {
    commit('SET_ALERT', true)
    if (window.gtad !== undefined) {
      window.gtag('config', 'UA-113745207-1', { 'page_path': payload.url, 'page_title': payload.title })
    }
  }
}



