<template lang="html">
  <BaseFeatureWrapper title="Reservas">
    <v-tabs v-model="active"  color="blue" slider-color="blue" grow style="height:100%">
      <v-tab :ripple="false">
        Confirmadas
      </v-tab>
      <v-tab :ripple="false">
        Pendentes
      </v-tab>
      <v-tab-item>
        <SpaceBox :space="space" v-for="space in list" :key="space.id" :email="true"/>
      </v-tab-item>
      <v-tab-item>
        <SpaceBox :space="space" v-for="space in list" :key="space.id"/>
      </v-tab-item>
    </v-tabs>
    <v-btn absolute dark fab right bottom @click="next" color="blue">
      <v-icon>add</v-icon>
    </v-btn>
    <v-layout>
      <v-flex xs12 class="text-xs-center">
      </v-flex>
    </v-layout>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import SpaceBox from './SpaceBox'

export default {
  components: {
    BaseFeatureWrapper,
    SpaceBox
  },
  data () {
    return {
      list: [
        {
          id: '1',
          name: 'Churrasqueira',
          tax: 32,
          maxGuests: 10,
          image: 'https://a-static.mlcdn.com.br/618x463/churrasqueira-a-carvao-de-parede-metavila-650c-para-6-espetos/magazineluiza/216864400/60071664b709104064ec8f9f30fb3c65.jpg'
        },
        {
          id: '2',
          name: 'Espaço Gourmet',
          tax: 100,
          maxGuests: 20,
          image: 'https://www.tuacasa.com.br/wp-content/uploads/2015/11/VF-Archidesign-Deck-Gourmet-Lumis-00-730x548.jpg'
        }
      ],
      active: null,
      text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
    }
  },
  methods: {
    next () {
      this.$router.push('/Recurso/Reservas/Nova')
    }
  },
  mounted () {
    // this.getSpace('123')
  }
}
</script>

<style lang="css">
.btn--bottom.btn--absolute {
    bottom: 15px;
}
.ic-space-card {
  box-sizing: border-box;
  margin-top: 15px;
  border-bottom: 1px solid #bec5d6;
  padding-bottom: 10px;
}
.ic-space-card:last-child {
  border: 0px;
}
.ic-spaceTitle{
  font-size: 20px;
    color: #2c5589;
}
.blue .tabs__item--active{
  color: #3b73a4
}
.tabs__slider.blue {
  background: #3b73a4;
}
.btn.blue{
  background-color: #3b73a4 !important;
  border-color: #3b73a4 !important;
  color: white;
}
.tabs__items {
  height: calc(100% - 48px);
  overflow:scroll
}
</style>



