import Vue from 'vue'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import { sync } from 'vuex-router-sync'
import {
  Vuetify,
  VApp,
  VGrid,
  VCard,
  VExpansionPanel,
  VTextField,
  VCheckbox,
  VIcon,
  VDatePicker,
  VBtn,
  VDialog,
  VTabs,
  VAvatar,
  VSnackbar
} from 'vuetify'

import App from './App'
import router from './router'
import store from './store'

Vue.use(Vuetify, {
  components: {
    VApp,
    VGrid,
    VCard,
    VExpansionPanel,
    VTextField,
    VCheckbox,
    VIcon,
    VDatePicker,
    VBtn,
    VDialog,
    VTabs,
    VAvatar,
    VSnackbar
  }
})

Raven
  .config('https://a6ab69bcb9684fc499a5bb069842fd63@sentry.io/294452', {
    release: '1.1.0-beta'
  })
  .addPlugin(RavenVue, Vue)
  .install()

sync(store, router)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



