import actions from './actions'
import mutations from './mutations'
import getters from './getters'

export default {
  namespaced: true,
  state: {
    user: {
      photo: '',
      name: '',
      sindico: false,
      actualUnit: {
        id: '',
        name: '',
        condo: '',
        adm: {
          name: '',
          logo: '',
          colors: null
        }
      },
      units: [],
      otherCondo: false
    }
  },
  getters,
  mutations,
  actions
}



