<template lang="html">
  <transition name="fade">
    <div class="ic-home" v-show="loading" style="position:absolute;z-index:999;top:0;left:0;background:#f5f5f5;">
      <div class="ic-header" :style="colors.bgc">
        <div class="ic-logo">
          <img src="../assets/img/logo.png" alt="">
        </div>
        <div class="ic-hello" style="width:200px; margin:19px auto 8px auto;">
          <div class="ic-block white"></div>
        </div>
        <div class="ic-condo" style="width:250px; margin:auto;margin-bottom: 17px;">
          <div class="ic-block white"></div>
        </div>
        <div class="ic-unit" :style="colors.bgcDark">
          <div class="ic-unit-selected" style="width:150px;margin:auto">
            <div class="ic-block white"></div>
          </div>
        </div>
      </div>
      <div class="ic-body">
        <div class="ic-card" v-for="feature in 4">
          <div class="ic-image">
          </div>
          <div class="ic-title-box" style="height: 15px;width: 70px;">
            <div class="ic-title" :style="colors.font">
              <div class="ic-block grey"></div>
            </div>
            <div class="ic-description"></div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('user', ['colors']),
    ...mapGetters('loadingScreen', [
      'loading',
      'error'
    ])
  }
}
</script>

<style lang="css" scoped>
.ic-block{
  width: 100%;
  height: 10px;
  opacity: 0.5;
  -webkit-animation: pulse-opacity 2s infinite ease-in-out;
  -o-animation: pulse-opacity 2s infinite ease-in-out;
  -ms-animation: pulse-opacity 2s infinite ease-in-out;
  -moz-animation: pulse-opacity 2s infinite ease-in-out;
  animation: pulse-opacity 2s infinite ease-in-out;
}
.white{
  background-color: white !important;
  border-color: white !important;
}
.grey{
  background-color: grey !important;
  border-color: grey !important;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active em versões anteriores a 2.1.8 */ {
  opacity: 0;
}
.ic-fotter-adm{
  padding: 0px 15px;
  float: left;
  width: 100%;
}
.ic-fotter-adm>.ic-logo{
  text-align: center;
  float: left;
  width: 70px;
}
.ic-fotter-adm>.ic-logo>img{
  width: 100%;
}
.ic-fotter-adm>.ic-text{
  font-size: 12px;
  padding-left: 20px;
  float: left;
  box-sizing: border-box;
  width: calc(100% - 70px);
}

.ic-assistis{
  position: relative;
  width: 100%;
  text-transform: capitalize;
}
.ic-assistis .ic-title{
  font-size: 16px;
  padding-left: 10px;
  padding-left: 35px;
  text-transform: uppercase;
}
.ic-assistis .ic-icon{
  float: left;
  width: 25px;
  color: #fff;
  font-size: 25px;
  margin-top: -3px;
  position: absolute;
}
.ic-fotter{
  position: fixed;
  padding: 15px;
  background: #4527a1;
  color: #fff;
  bottom: 0;
  width: 100%;
  z-index: 2;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.ic-bullet{
  position: absolute;
  border-radius: 100%;
  color: #fff;
  width: 25px;
  font-size: 13px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
}
.ic-fotter .ic-bullet{
  top: -3px;
  right: -3px;
}
.ic-body .ic-bullet{
  top: 8px;
  right: 8px;
}
.ic-body{
  padding: 15px 5px;
  float: left;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.ic-card{
  position: relative;
  box-sizing: border-box;
  float: left;
  padding: 5px 10px;
  background: #fff;
  width: 47%;
  margin-bottom: 10px;
  height: 100px;
  border-bottom: 1px solid #d1d1d1;
}
.ic-card-extended{
  position: relative;
  box-sizing: border-box;
  float: right;
  padding: 5px 10px;
  margin-bottom: 10px;
  background: #fff;
  width: 100%;
  height: 60px;
  border-bottom: 1px solid #d1d1d1;
}
.ic-card-extended>.ic-title-box>.ic-title{
  padding-top: 10px;
}
.ic-card>.ic-title-box{
  position: absolute;
  bottom: 8px;
}
.ic-card-extended .ic-image{
  position: absolute;
  right: 10px;
}
.ic-card .ic-description, .ic-card-extended .ic-description{
  color: #868686;
  font-size: 9px;
}
.ic-card:nth-child(odd){
  float: right;
}
.ic-card .ic-image div, .ic-card-extended .ic-image div{
  margin-top: 8px;
  height: 30px;
  font-size: 31px;
  margin-bottom: 25px;
}
.ic-header{
  padding-top: 25px;
  text-align: center;
  font-family: "Roboto";
  position: relative;
}
.ic-header>.ic-menu-icon{
  top: 23px;
  left: 20px;
  font-size: 32px;
  position: absolute;
  color: #fff;
}
.ic-header>.ic-logo{
  width: 207px;
  height: 33.56px;
  margin: auto;
}
.ic-header>.ic-logo>img{
  width: 100%;
}
@media screen and (max-width: 340px) {
  .ic-header>.ic-menu-icon{
    top: 23px;
    left: 20px;
    font-size: 28px;
    position: absolute;
    color: #fff;
  }
  .ic-header>.ic-logo{
    width: 190px;
    height: 30.8px;
  }
}
.ic-header .ic-condo {
  color: #fff;
  opacity: 0.7;
  text-transform: capitalize;
  font-size: 14px;
}
.ic-header>.ic-hello{
  color: #fff;
  font-size: 18px;
}
.ic-header .ic-unit{
  position: relative;
  padding: 10px;
}
.ic-unit .ic-unit-selected {
  padding-top: 5px;
padding-bottom: 5px;
  color:#fff;
}
.ic-unit>.ic-unit-selected>.ic-icon{
  position: absolute;
  right: 20px;
  height: 18px;
  font-size: 16px;
  top: 10px;
}
.ic-header .ic-unit select{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
}
.ic-wrapper{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.ic-home{
  overflow: scroll;
  padding-bottom: 50px;
  box-sizing: border-box;
}

.transitionSindico,.transitionUnit{
  position: absolute;
  left: 0;
  width: 100%;
  -webkit-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -o-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -ms-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -moz-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
}
.transitionSindico{
  opacity: 0;
  animation-delay: 5s;
  -webkit-animation-delay: 5s;
}
@-webkit-keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}
@keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}

@-webkit-keyframes pulse-opacity {
    0% { opacity: 0.3; }
    50% { opacity: 0.6; }
    100% { opacity: 0.3; }
}
@keyframes pulse-opacity {
    0% { opacity: 0.3; }
    50% { opacity: 0.6; }
    100% { opacity: 0.3; }
}
</style>



