<template lang="html">
  <BaseFeatureWrapper title="Confirme sua reserva">
    <SpaceBox :space="space" />
    <v-layout row wrap style="border-bottom: 1px solid #bec5d6; padding-bottom: 10px;">
      <v-flex xs5 class="ic-label">
        Data da reserva
      </v-flex>
      <v-flex xs7 class="ic-label">
        Periodo
      </v-flex>
      <v-flex xs5 class="ic-text">
        {{ formatedBookingDate }}
      </v-flex>
      <v-flex xs7 class="ic-text">
        {{ booking.hour.description }}
      </v-flex>
    </v-layout>
      <v-flex xs12>
        <h4 style="margin: 10px 0px 2px 0px;font-size: 18px;color: #5c6e78;">
          Observações da reserva
        </h4>
      </v-flex>
      <v-flex xs12 class="ic-observation">
        <p>
          Espaço liberado das {{ booking.hour.time.start }} - até {{ booking.hour.time.end }}
        </p>
        <p v-if="space.antecedence.cancel !== null">
          Cancelamento em até {{ space.antecedence.cancel }} dia<span v-if="space.antecedence.cancel > 1">s</span>
        </p>
      </v-flex>
      <v-flex xs12>
        <v-text-field
           name="input-7-1"
           label="Observações extra"
           v-model="observation"
           :placeholder="`Churrasco da Família, Chegar a partir das ${booking.hour.time.start} horas, homens trazer bebida, a festa vai até as ${booking.hour.time.end}`"
           multi-line
           hide-details
         ></v-text-field>
      </v-flex>
    </v-layout>
    <v-flex xs12>
      <p>
        <strong class="red--text">Importante:</strong> Ao reservar o ambiente {{ space.name }}, declaro que estou ciente dos <span class="blue--text" @click="openTerm">termos de uso</span> e que assumo total responsabilidade pela reserva requerida
      </p>
    </v-flex>
  <v-flex offset-xs1 xs10>
    <v-btn large block color="blue">Reservar</v-btn>
  </v-flex>
 <v-dialog v-model="termModal" scrollable>
      <v-card>
        <v-card-title>Termos de uso</v-card-title>
        <v-card-text>
            {{ space.useTerm }}
        </v-card-text>
      </v-card>
    </v-dialog>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import SpaceBox from './SpaceBox'
import { mapGetters, mapActions } from 'vuex'
import moment from 'moment'

export default {
  components: {
    BaseFeatureWrapper,
    SpaceBox
  },
  data () {
    return {
      observation: '',
      useTerm: false,
      termModal: false
    }
  },
  methods: {
    ...mapActions('booking', ['getSpace']),
    openTerm () {
      this.termModal = true
    }
  },
  computed: {
    ...mapGetters('booking', ['booking', 'space']),
    formatedBookingDate () {
      moment.locale('pt-br')
      return moment(this.booking.date).format('DD/MM/YY')
    }
  }
}
</script>

<style lang="css">
.ic-observation p{
  font-size: 14px;
  margin: 2px 0px;
}
.ic-observation{
  margin-bottom: 10px;
}
.red--text{
  color: #fb1313;
}
.blue--text{
  color: #3b73a4;
}
.btn.blue{
  background-color: #3b73a4 !important;
  border-color: #3b73a4 !important;
  color: white;
}
.ic-label{
  color: #2c5589;
  font-size: 12px;
  padding: 10px;
  box-sizing: border-box;
  padding-left: 0px;
  padding-bottom: 0px;
}
.ic-text{
  color: #5c6e78;
  font-size: 20px;
}
</style>



