import axios from 'axios'
export default {
  get: async ({commit}) => {
    const { data: user } = await axios.get('https://api.myjson.com/bins/12a6yd')
    commit('SET_USER', user)
    return true
  },
  changeSessionByUnit: async ({state, commit, dispatch}, payload) => {
    const { data: result } = await axios.post('/Home/trocaSessaoPorUnidadeAgregada', { unidade: payload })
    return result
  },
  changeArea: async ({state, commit, dispatch}, payload) => {
    const { data: result } = await axios.post(payload.url)
    return result
  }
}



