<template lang="html">
  <div class="ic-feature" :style="colors.bodyBgc" v-if="true">
    <img class="ic-bar" src="../../assets/img/barra.png">
    <div class="ic-header-iframe"  :style="colors.bodyBgc">
      <div class="ic-icon back" :style="colors.font" v-on:click="goBack">
        <span class="icon-down-arrow"></span>
      </div>
      <div class="ic-feature-name" :style="colors.font">
        {{ title }}
      </div>
    </div>
    <div class="ic-feature-container">
      <slot></slot>
    </div>
    <!-- <router-view class="ic-feature-container"></router-view> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: ['title'],
  computed: {
    ...mapGetters('user', ['colors'])
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="css">
.ic-feature-container{
  margin: 0px 10px;
  padding: 60px 10px 0px 10px;
  background-color: white;
  height: 100%;
  overflow: scroll;
  box-sizing: border-box;
}
.ic-feature{
  height:100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f0f3fa;
}
.ic-header-iframe{
  padding-top: 8px;
  padding-bottom: 14px;
  float: left;
  width: 100%;
  position: absolute;
  z-index: 1;
  background-color: #f0f3fa;
}
.ic-header-iframe>.ic-feature-name{
  float: left;
  font-size: 20px;
  padding-top: 12px;
}
.ic-header-iframe>.back{
  width: 23px;
  display: block;
  margin-left: 15px;
  margin-right: 8px;
  float: left;
  margin-top: 10px;
  font-size: 23px;
  transform: rotate(90deg);
}
.loading{
  position: absolute;
  top: 0;
}
.ic-bar{
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 2px;
}
</style>



