export default {
  booking (state) {
    return state.booking
  },
  spaceList (state) {
    return state.spaceList
  },
  space (state) {
    return state.space
  },
  hourList (state) {
    return state.hourList
  },
  events (state) {
    return state.space.date.unavaliable.concat(state.space.date.partially)
  },
  hasUnavaliableDay (state) {
    return state.space.date.unavaliable.length > 0
  },
  loadingHourList (state) {
    return state.loadingHourList
  },
  hourModal (state) {
    return state.hourModal
  },
  // isUnavaliableDay (state) {
  //   return state.space.date.unavaliable.findIndex(elem => elem === state.booking.date) > -1
  // },
  hasPartiallyDay (state) {
    return state.space.date.partially.length > 0
  },
  monthYear (state) {
    return state.monthYear
  }
}



