import axios from 'axios'
export default {
  getAllSpaces: async ({commit}, payload) => {
    const { data: space } = await axios.get('static/mock/Reserva/ObterAllSpaces.json')
    commit('SET_SPACE_LIST', space)
    return true
  },
  getSpace: async ({commit}, payload) => {
    const { data: space } = await axios.get('static/mock/Reserva/ObterSpace.json')
    commit('SET_SPACE', space)
    return true
  },
  getHourList: async ({commit}, payload) => {
    const { data: hourList } = await axios.get('static/mock/Reserva/2018-03-23.json')
    commit('SET_HOUR_LIST', hourList)
    return true
  },
  getEventDays: async ({commit}, payload) => {
    const { data: daysList } = await axios.get('static/mock/Reserva/' + payload + '.json')
    commit('SET_DAYS_LIST', daysList)
    return true
  },
  setVisualizationMonth: async ({ commit, dispatch, getters }, payload) => {
    if (getters.monthYear.findIndex(elem => elem === payload) > -1) {
      return
    }
    commit('SET_MONTH_YEAR', payload)
    commit('SET_LOADING_HOUR_LIST', true)
    try {
      await dispatch('getEventDays', payload)
    } catch (e) {
      commit('SET_LOADING_HOUR_LIST', false)
      return false
    }
    commit('SET_LOADING_HOUR_LIST', false)
    return true
  },
  setSpace: async ({ dispatch, commit }, payload) => {
    // loading space
    commit('CLEAR_MONTH_YEAR')
    commit('RESET_BOOKING_DATE')
    commit('RESET_BOOKING_HOUR')
    commit('RESET_HOUR_LIST')
    await dispatch('getSpace', payload)
    // loading space false
    const monthYear = new Date().toISOString().substr(0, 7)
    await dispatch('setVisualizationMonth', monthYear)
    return true
  },
  setDate: async ({dispatch, commit, getters}, payload) => {
    if (getters.booking.date === payload) {
      commit('SET_HOUR_MODAL', true)
      return
    }
    if (getters.booking.hour !== null) {
      commit('RESET_BOOKING_HOUR')
    }
    if (getters.hourList.length > 0) {
      commit('RESET_HOUR_LIST')
    }
    commit('SET_BOOKING_DATE', payload)
    commit('SET_LOADING_HOUR_LIST', true)
    try {
      await dispatch('getHourList', payload)
      commit('SET_HOUR_MODAL', true)
    } catch (e) {
      commit('SET_BOOKING_DATE', null)
      commit('SET_LOADING_HOUR_LIST', false)
      return false
    }
    commit('SET_LOADING_HOUR_LIST', false)
    return true
  }
}



