<template lang="html">
  <div class="ic-home">
    <div class="ic-header" :style="colors.bgc">
      <img class="ic-logo" src="../../assets/img/logo.png" alt="">
      <div class="ic-hello">
        Olá, <span style="text-transform: capitalize;">{{ user.name | shortName }}</span>
      </div>
      <div class="ic-icon" v-on:click="openMenu">
        <span class="icon-menu"></span>
      </div>
      <div class="ic-condo">
        {{ user.actualUnit.condo.name }}
      </div>
      <div class="ic-unit" :style="colors.bgcDark">
        <select class="" name="" v-model="unitSelected" v-if="user.units.length !== 0 || !user.otherCondo">
          <option :value="user.actualUnit.name" selected disabled>{{ user.actualUnit.name }}</option>
          <option :value="unit.id" v-for="unit in user.units">{{ unit.name }}</option>
          <option value="otherCondo">Outro condomínio</option>
        </select>
        <div class="ic-unit-selected">
          <span v-if="transitionSindicoUnit" :class="{'transitionSindico': transitionSindicoUnit}">{{ user.levelLabel }}</span><span :class="{'transitionUnit': transitionSindicoUnit}">{{ user.actualUnit.name }}</span>
          <div class="ic-icon" v-if="user.units.length !== 0">
            <span class="icon-down-arrow"></span>
          </div>
        </div>
      </div>
    </div>
    <div class="ic-body">
      <div class="ic-extended-card" v-if="area !== false" v-on:click="changeArea(area)">
        <div class="ic-icon">
          <div :class="area.icon" :style="colors.icon"></div>
        </div>
        <div class="ic-title-box">
          <div class="ic-title" :style="colors.font">
            {{ area.title }}
          </div>
          <div class="ic-description">{{ area.description }}</div>
        </div>
      </div>
      <div class="ic-card-container">
        <div class="ic-card" v-for="feature in mainFeatures" v-on:click="goTo(feature)">
          <div class="ic-bullet" v-if="feature.bullet > 0" :style="colors.bullet">
            {{ feature.bullet }}
          </div>
          <div class="ic-icon">
            <div :class="feature.icon" :style="colors.icon"></div>
          </div>
          <div class="ic-title-box">
            <div class="ic-title" :style="colors.font">
              {{ feature.title }}
            </div>
            <div class="ic-description">{{ feature.description }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="ic-fotter-adm" v-if="false">
      <div class="ic-logo">
        <img :src="admImage" alt="">
      </div>
      <div class="ic-text">
        ©{{ currentYear }} iCondomínio é uma solução da Troad Tecnologia. Todos os direitos reservados.
      </div>
    </div>
    <div class="ic-fotter" v-if="assistis !== false" v-on:click="goTo(assistis)">
      <div class="ic-assistis">
        <div class="ic-icon">
          <span :class="assistis.icon"></span>
        </div>
        <div class="ic-title">
          {{ assistis.description }}
        </div>
        <div class="ic-bullet" :style="colors.bullet" v-if="assistis.bullet">
          {{ assistis.bullet }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('user', ['colors', 'user', 'transitionSindicoUnit', 'admImage']),
    ...mapGetters('features', ['mainFeatures', 'area', 'assistis']),
    unitSelected: {
      get () {
        return this.user.actualUnit.id
      },
      set (value) {
        this.changeUnit(value)
      }
    },
    currentYear () {
      return (new Date()).getFullYear()
    }
  },
  filters: {
    shortName (value) {
      if (value !== null || value !== '' || value !== undefined) {
        let name = value.split(' ')
        return name.length > 1 ? name[0] + ' ' + name[1] : name[0]
      }
    }
  },
  methods: {
    ...mapActions('shared', ['changeUnit', 'changeArea']),
    ...mapActions('features', ['goToAnalitics']),
    openMenu () {
      this.$router.push('/Menu')
    },
    goTo (value) {
      if (value.href) {
        this.goToAnalitics(value)
      } else {
        this.$router.push(value.url)
      }
    }
  }
}
</script>

<style lang="css" scoped>
.ic-fotter-adm{
  padding: 10px 15px;
  position: absolute;
  bottom: 0px;
  background-color: #fff;
  float: left;
  width: 100%;
  box-sizing: border-box;
}
.ic-fotter-adm>.ic-logo{
  text-align: center;
  float: left;
  width: 70px;
}
.ic-fotter-adm>.ic-logo>img{
  width: 100%;
}
.ic-fotter-adm>.ic-text{
  font-size: 12px;
  padding-left: 20px;
  float: left;
  box-sizing: border-box;
  width: calc(100% - 70px);
}

.ic-assistis{
  position: relative;
  width: 100%;
  text-transform: capitalize;
}
.ic-assistis .ic-title{
  font-size: 16px;
  padding-left: 10px;
  padding-left: 35px;
  text-transform: uppercase;
}
.ic-assistis .ic-icon{
  float: left;
  width: 25px;
  color: #fff;
  font-size: 25px;
  margin-top: -3px;
  position: absolute;
}
.ic-fotter{
  position: fixed;
  padding: 15px;
  background: #4527a1;
  color: #fff;
  bottom: 0;
  width: 100%;
  z-index: 2;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.ic-bullet{
  position: absolute;
  border-radius: 100%;
  color: #fff;
  width: 25px;
  font-size: 13px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
}
.ic-fotter .ic-bullet{
  top: -3px;
  right: -3px;
}
.ic-body .ic-bullet{
  top: 8px;
  right: 8px;
}
.ic-body{
  padding: 15px 5px;
  float: left;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.ic-card{
  position: relative;
  box-sizing: border-box;
  float: left;
  padding: 5px 10px;
  background: #fff;
  width: 49%;
  margin-bottom: 10px;
  height: 100px;
  border-bottom: 1px solid #d1d1d1;
}
.ic-extended-card{
  position: relative;
  box-sizing: border-box;
  float: right;
  padding: 5px 10px;
  margin-bottom: 10px;
  background: #fff;
  width: 100%;
  height: 60px;
  border-bottom: 1px solid #d1d1d1;
}
.ic-extended-card>.ic-title-box>.ic-title{
  padding-top: 10px;
}
.ic-card>.ic-title-box{
  position: absolute;
  bottom: 8px;
}
.ic-extended-card .ic-icon{
  position: absolute;
  right: 10px;
}
.ic-card .ic-description, .ic-extended-card .ic-description{
  color: #868686;
  font-size: 9px;
}
.ic-card-container .ic-card:nth-child(odd){
  float: left;
  margin-right: 2%;
}
.ic-card .ic-icon div, .ic-extended-card .ic-icon div{
  margin-top: 8px;
  height: 30px;
  font-size: 31px;
  margin-bottom: 25px;
}
.ic-header{
  padding-top: 25px;
  text-align: center;
  font-family: "Roboto";
  position: relative;
}
.ic-header>.ic-icon{
  top: 23px;
  left: 20px;
  font-size: 32px;
  position: absolute;
  color: #fff;
}
.ic-header>.ic-logo{
  width: 207px;
}
@media screen and (min-width: 583px) {
  .ic-fotter-adm>.ic-text{
    padding-top: 5px;
  }
}
@media screen and (max-width: 340px) {
  .ic-header>.ic-icon{
    top: 23px;
    left: 20px;
    font-size: 28px;
    position: absolute;
    color: #fff;
  }
  .ic-header>.ic-logo{
    width: 190px;
  }
}
.ic-header .ic-condo {
  color: #fff;
  opacity: 0.7;
  text-transform: capitalize;
  font-size: 14px;
  padding-bottom: 10px;
}
.ic-header>.ic-hello{
  color: #fff;
  padding-top: 15px;
  font-size: 18px;
}
.ic-header .ic-unit{
  position: relative;
  padding: 10px;
}
.ic-unit .ic-unit-selected {
  min-height: 20px;
  color:#fff;
}
.ic-unit>.ic-unit-selected>.ic-icon{
  position: absolute;
  right: 20px;
  height: 18px;
  font-size: 16px;
  top: 10px;
}
.ic-header .ic-unit select{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
}
.ic-wrapper{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.ic-home{
  overflow: scroll;
  padding-bottom: 50px;
  box-sizing: border-box;
}

.transitionSindico,.transitionUnit{
  position: absolute;
  left: 0;
  width: 100%;
  -webkit-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -o-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -ms-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -moz-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
}
.transitionSindico{
  opacity: 0;
  animation-delay: 5s;
  -webkit-animation-delay: 5s;
}
@-webkit-keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}
@keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}
</style>



