import Raven from 'raven-js'
export default {
  SET_USER (state, payload) {
    state.user = payload
    Raven.setUserContext({
      email: payload.email,
      username: payload.name
    })
    Raven.setExtraContext({
      mobile: true,
      sindico: payload.sindico,
      unidadeAtual: payload.actualUnit.name
    })
  }
}



