<template lang="html">
  <BaseFeatureWrapper title="Escolha um espaço">
    <SpaceBox :space="space" v-for="space in spaceList" :key="space.id" @click="next"/>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import SpaceBox from './SpaceBox'
import { mapGetters, mapActions } from 'vuex'
export default {
  components: {
    BaseFeatureWrapper,
    SpaceBox
  },
  methods: {
    ...mapActions('booking', ['getAllSpaces', 'setSpace']),
    next (id) {
      this.setSpace(id)
      this.$router.push('/Recurso/Reservas/Nova/Datas')
    }
  },
  computed: {
    ...mapGetters('booking', ['spaceList'])
  },
  async mounted () {
    await this.getAllSpaces()
  }
}
</script>



