import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/'
import Menu from '@/components/Menu/'
import BaseFeatureTransition from '@/components/Feature/BaseFeatureTransition'

import Avaliacao from '@/components/Feature/Avaliacao/'
import Conveniencias from '@/components/Feature/Conveniencias/'
import Upgrade from '@/components/Feature/Upgrade/'
import TermoDeUso from '@/components/Feature/TermoDeUso/'
import Politica from '@/components/Feature/Politica/'

import Reservas from '@/components/Feature/Reservas/'
import ListaEspacos from '@/components/Feature/Reservas/NewSpaceSelection'
import ReservasNova from '@/components/Feature/Reservas/new'
import ReservasConfirmacao from '@/components/Feature/Reservas/confirmation'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Menu',
      name: 'Menu',
      component: Menu
    },
    {
      path: '/Recurso/Avaliacao',
      name: 'Avaliacao',
      component: Avaliacao
    },
    {
      path: '/Recurso/Conveniencias',
      name: 'Conveniencias',
      component: Conveniencias
    },
    {
      path: '/Recurso/Upgrade',
      name: 'Upgrade',
      component: Upgrade
    },
    {
      path: '/Recurso/Reservas',
      component: BaseFeatureTransition,
      children: [
        {
          path: '/',
          component: Reservas
        },
        {
          path: 'Nova',
          component: BaseFeatureTransition,
          children: [
            {
              path: '/',
              name: 'Espacos',
              meta: {
                level: 1
              },
              component: ListaEspacos
            },
            {
              path: 'Datas',
              name: 'Data-reserva',
              component: ReservasNova
            },
            {
              path: 'Confirmacao',
              name: 'Confirmcao-Reserva',
              component: ReservasConfirmacao
            }
          ]
        }
      ]
    },
    {
      path: '/Recurso/TermoDeUso',
      name: 'TermoDeUso',
      component: TermoDeUso
    },
    {
      path: '/Recurso/Politica',
      name: 'Politica',
      component: Politica
    },
    {
      path: '/Logout',
      name: 'Logout',
      beforeEnter: (to, from, next) => {
        window.location.href = '/Logout'
      }
    }
  ]
})



