export default {
  user (state) {
    return state.user
  },
  admImage (state) {
    return state.user.actualUnit.adm.logo
  },
  transitionSindicoUnit (state) {
    return state.user.level > 2 && state.user.actualUnit.name !== ''
  },
  colors (state) {
    if (state.user.actualUnit.adm.colors !== null) {
      return state.useractualUnit.adm.colors
    } else {
      // return {
      //   bgc: {
      //     backgroundColor: '#af0057'
      //   },
      //   bgcDark: {
      //     backgroundColor: '#870e4b'
      //   },
      //   icon: {
      //     color: '#af0057'
      //   },
      //   menuIcon: {
      //     color: '#870e4b'
      //   },
      //   font: {
      //     color: '#870e4b'
      //   },
      //   bullet: {
      //     backgroundColor: '#ff3201'
      //   }
      // }
      return {
        bgc: {
          backgroundColor: '#2c5589'
        },
        bgcDark: {
          backgroundColor: '#244570'
        },
        icon: {
          color: '#585858'
        },
        menuIcon: {
          color: '#47688f'
        },
        font: {
          color: '#47688f'
        },
        bullet: {
          backgroundColor: '#ff3201'
        }
      }
    }
  }
}



