<template>
  <v-app light>
    <v-container fluid pa-0 ma-0>
      <transition :name="transitionName">
        <router-view class="child-view"></router-view>
      </transition>
    </v-container>
    <v-snackbar v-model="snackbar">
      Recurso não disponível
      <v-btn flat color="pink" @click.native="snackbar = false" :ripple="false"><v-icon>close</v-icon></v-btn>
    </v-snackbar>
    <loading-screen/>
  </v-app>
</template>

<script>
import { mapActions } from 'vuex'
import loadingScreen from '@/components/loadingScreen'

export default {
  components: {
    loadingScreen
  },
  data () {
    return {
      transitionName: 'slide-right',
      snackbar2: false
    }
  },
  methods: {
    ...mapActions('shared', ['getInfo'])
  },
  computed: {
    snackbar: {
      get () {
        return this.$store.getters['features/featuresAlert']
      },
      set (value) {
        this.$store.commit('features/SET_ALERT', value)
      }
    }
  },
  created () {
    this.getInfo()
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.trim().split('/').length
      const fromDepth = from.path.trim().split('/').length
      if (to.name === 'Menu' && from.name === 'Home') {
        this.transitionName = toDepth < fromDepth ? 'slide-left' : 'slide-right'
      } else {
        this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      }
    }
  }
}
</script>

<style lang="stylus" media='screen'>
.tab-transition-enter{-webkit-transform:translate(100%);transform:translate(100%)}.tab-transition-enter-to{-webkit-transform:translate(0);transform:translate(0)}.tab-transition-leave,.tab-transition-leave-active{position:absolute;top:0}.tab-transition-leave-to{position:absolute}.tab-reverse-transition-enter,.tab-transition-leave-to{-webkit-transform:translate(-100%);transform:translate(-100%)}.tab-reverse-transition-leave,.tab-reverse-transition-leave-to{top:0;position:absolute;-webkit-transform:translate(100%);transform:translate(100%)}
.pa-0{
  padding: 0!important;
}
.child-view {
  position: absolute;
  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
}
#app {
  font-family: 'Roboto'
}
.btn {
  border: 0
}
.text-xs-center{
  text-align: center;
}
.date-picker-table__event.green{
  background-color: #66bb6a!important
}
.date-picker-table__event.red {
  background-color: #f44336!important;
}
.date-picker-table__event.yellow {
  background-color: #ffeb3b!important;
}
body{
  margin: 0;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
*:focus {
  outline: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
input:focus, textarea:focus {
  outline: none;
  -webkit-touch-callout: text;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
.ic-home{
  overflow: scroll;
  padding-bottom: 50px;
  box-sizing: border-box;
}
#app,.ic-wrapper,.ic-menu,.ic-home{
  height: 100%;
  width: 100%;
}

/* vuetify needs */
.input-group.input-group--text-field input, .input-group--text-field.input-group--multi-line textarea{
  border: hidden;
}
.tabs__item{
  box-sizing: border-box
}
.card__text{
  box-sizing: border-box;
}
.snack__content{
  padding: 0;
  padding-left: 10px;
}
</style>



