<template lang="html">
  <v-dialog :value="hourModal" @input="SET_HOUR_MODAL" scrollable max-width="300px">
    <v-card>
      <v-card-title>Selecione o período</v-card-title>
      <v-card-text>
        <div class="avaliableHours-wrapper">
          <div :class="{'hour-box': true, 'notAvaliable': !hour.avaliable }" v-for="hour in hourList" :key="hour.id" @click="SET_BOOKING_HOUR(hour)">
            <!-- {{ hour.description }} horario de funcinamento <br> -->
            <div :class="{'hour-check': true, 'checked': booking.hour === hour}">
              <v-icon v-show="booking.hour === hour">check</v-icon>
            </div>
            <span class="hourTime">{{ hour.time.start }} até {{ hour.time.end }}</span>
            <span>{{ hour.description }}</span> <span v-if="hour.avaliable">disponivel</span><span v-else>insponivel</span> <v-icon class="noble" v-if="hour.noble">warning</v-icon>
          </div>
        </div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  methods: {
    ...mapMutations('booking', ['SET_BOOKING_HOUR', 'SET_HOUR_MODAL'])
  },
  computed: {
    ...mapGetters('booking', ['booking', 'hourModal', 'hourList'])
  }
}
</script>

<style lang="css">
.hour-check{
  height: 25px;
  width: 25px;
  border: 1px solid #8a8a8a;
  border-radius: 100%;
  position: absolute;
  left: 5px;
  top: 13px;
}
.noble{
  color: #eaa624!important;
  position: absolute;
  top: 15px;
  right: 5px;
}
.notAvaliable .hour-check{
  border-color: rgba(169, 169, 169, 0.8)
}
.notAvaliable .noble{
  color: rgba(234, 166, 36, 0.8)!important
}
.hour-check i{
  font-size: 17px;
  padding-left: 4px;
  padding-top: 4px;
}
.checked{
  border-color: #4caf50
}
.checked i{
  color: #4caf50!important
}
.hour-box{
  position: relative;
  padding-top: 12px;
  border-bottom: 1px solid darkgrey;
  padding-left: 40px;
  padding-bottom: 20px;
}
.hour-box:last-child{
  border-bottom: 0px solid;
}

.hourTime{
  position: absolute;
  bottom: 6px;
  left: 41px;
  font-size: 12px;

}
.avaliableHours-wrapper{
  margin: auto;
}
.notAvaliable{
  color: rgba(169, 169, 169, 0.8);
}
</style>



