<template lang="html">
  <BaseFeatureWrapper title="Upgrade">
    <v-layout row wrap>
      <p><span class="ic-user-name">{{ user.name }}</span>, entre em contato conosco e assine a versão mais completa para seu condomínio</p>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs8>
        <v-text-field :mask="upgrade.phone.length === 11 ? '(##) #####-####' : '(##) ####-#####'" label="Telefone para contato" v-model="upgrade.phone"></v-text-field>
      </v-flex>
      <v-flex xs12 style="padding-bottom:20px;">
        <p>
          Quais são os recursos que mais interessam ao seu condomínio?
        </p>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs6 v-for="feature in avaliableFeatures" :key="feature">
        <v-checkbox :label="feature" :value="feature" v-model="upgrade.features"></v-checkbox>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
        <v-text-field multi-line label="Mensagem" v-model="upgrade.text"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12 class="ic-button">
        <button class="ic-sendButton" type="button" name="button" @click="sendRating">Enviar</button>
      </v-flex>
    </v-layout>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import { mapGetters } from 'vuex'
import axios from 'axios'

export default {
  components: {
    BaseFeatureWrapper
  },
  data () {
    return {
      upgrade: {
        features: [],
        phone: '',
        text: ''
      },
      avaliableFeatures: ['Mensagem', 'Reserva', 'Notícias', 'Enquetes', 'Arquivos', 'Serviços', 'Cartas', 'Mudanças', 'Consultar', 'Cadastros', 'Alertas', 'Outros']
    }
  },
  computed: {
    ...mapGetters('user', ['user'])
  },
  methods: {
    sendRating () {
      axios.post('https://cupons-23a86.firebaseio.com/.json', {
        text: this.upgrade.text,
        features: this.upgrade.features,
        phone: this.upgrade.phone,
        user: this.user.name,
        levelLabel: this.user.levelLabel,
        adm: this.user.actualUnit.adm.name,
        condo: this.user.actualUnit.condo,
        unit: this.user.actualUnit.name
      }).then(result => this.clear)
    },
    clear () {
      this.upgrade = {
        features: [],
        phone: '',
        text: ''
      }
    }
  },
  mounted () {
    this.upgrade.text = 'digite sua mensagem aqui'
  }
}
</script>

<style lang="css">
.ic-user-name{
  text-transform: capitalize;
}
.ic-button{
  text-align: center;
  padding-bottom:20px;
}
.ic-button>.ic-sendButton{
  border-radius: 10px;
  background-color: #4b8ef9;
  padding: 9px 65px;
  color: #fff;
  font-size: 22px;
  text-transform: inherit;
  border: none;
  width: 100%;
  max-width: 320px;
}
</style>



