import actions from './actions'
import mutations from './mutations'
import getters from './getters'

export default {
  namespaced: true,
  state: {
    space: {
      id: '',
      name: '',
      antecedence: {
        min: null,
        max: null,
        cancel: null
      },
      tax: 0,
      date: {
        unavaliable: [],
        partially: []
      },
      useTerm: ''
    },
    booking: {
      date: null,
      hour: null,
      unit: null
    },
    hourList: [],
    loadingHourList: false,
    hourModal: false,
    monthYear: [],
    spaceList: []
  },
  getters,
  mutations,
  actions
}



