export default {
  getInfo: async ({commit, dispatch}) => {
    commit('loadingScreen/SET_LOADING', true, {root: true})
    await dispatch('user/get', null, {root: true})
    await dispatch('features/get', null, {root: true})
    // setTimeout(() => { commit('loadingScreen/SET_LOADING', false, {root: true}) }, 3000000)
    commit('loadingScreen/SET_LOADING', false, {root: true})
  },
  changeUnit: async ({commit, dispatch}, payload) => {
    if (payload === 'otherCondo') {
      window.location.href = '/Home/CondominiosVinculados'
      return
    }
    commit('loadingScreen/SET_LOADING', true, {root: true})
    await dispatch('user/changeSessionByUnit', payload, {root: true})
    await dispatch('getInfo')
    commit('loadingScreen/SET_LOADING', false, {root: true})
  },
  changeArea: async ({commit, dispatch}, payload) => {
    commit('loadingScreen/SET_LOADING', true, {root: true})
    await dispatch('user/changeArea', payload, {root: true})
    await dispatch('getInfo')
    commit('loadingScreen/SET_LOADING', false, {root: true})
  }
}



