<template lang="html">
  <v-layout row wrap class="ic-space-box" @click="selectSpace(space.id)">
    <v-flex xs3 class="text-xs-center">
      <v-avatar size="65" class="grey lighten-4">
        <img v-if="space.image !== null" :src="space.image" alt="avatar">
        <span v-else class="white--text headline">{{ space.name[0] }}</span>
      </v-avatar>
    </v-flex>
    <v-flex xs9 style="padding-left: 10px;">
      <div class="ic-spaceTitle">
        <strong>{{ space.name }}</strong>
      </div>
      <span v-if="space.tax !== null">Taxa de uso: {{ space.tax | currency }}</span><br>
      Máximo de convidados: {{ space.maxGuests }}
    </v-flex>
    <v-flex xs12 class="text-xs-center" v-if="email === true">
      <v-btn @click.native="whatsapp" flat color="primary"><v-icon left>email</v-icon> Enviar convites</v-btn>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  props: ['space', 'email'],
  methods: {
    selectSpace (id) {
      this.$emit('click', id)
    },
    whatsapp () {
      window.location = 'whatsapp://send?text=Estou te convidando para ' + this.space.name + ' venha conhecer https://www.icondominio.com.br/convite/3123123'
    }
  },
  filters: {
    currency (value) {
      if (typeof value !== 'number') return value
      return value.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
    }
  }
}
</script>

<style lang="css">
.ic-space-box {
  box-sizing: border-box;
  margin-top: 15px;
  border-bottom: 1px solid #bec5d6;
  padding-bottom: 10px;
}
.ic-space-box div {
  box-sizing: border-box;
}
.ic-space-box:last-child {
  border: 0px;
}
.ic-spaceTitle{
  font-size: 20px;
  color: #2c5589;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.avatar {
  background: #dcdcdc;
  font-size: 26px;
  color: #757575;
}
</style>



