<template lang="html">
    <div class="ic-menu">
      <div class="ic-header-fixed">
        <img class="ic-bar" src="../../assets/img/barra.png" alt="">
        <div class="ic-menu-header _p-15">
          <div class="ic-photo">
            <img v-bind:src="user.photo" alt="">
          </div>
          <div class="ic-user-info">
            <div class="ic-hello" v-bind:style="colors.font">
              <span style="text-transform: capitalize;">{{ user.name | shortName }}</span>
            </div>
            <div class="ic-unit">
              <span v-if="transitionSindicoUnit" :class="{'transitionSindico': transitionSindicoUnit}">{{ user.levelLabel }}</span><span :class="{'transitionUnit': transitionSindicoUnit}">{{ user.actualUnit.name }}</span>
            </div>
          </div>
          <div class="ic-icon" v-on:click="closeMenu">
            <span class="icon-fechar" v-bind:style="colors.menuIcon"></span>
          </div>
          <div class="ic-edit-info" v-on:click="goTo({href: true, url:'/Cadastro/Init'})">
            <div class="ic-icon">
              <span class="icon-edit" v-bind:style="colors.menuIcon"></span>
            </div>
            <span>
              Editar perfil
            </span>
          </div>
        </div>
      </div>
      <div class="ic-menu-body">
        <ul class="ic-list-wrapper">
          <template v-for="feature in features">
            <div class="ic-list-link">
              <li v-if="feature.childrens === null" class="ic-link" v-on:click="goTo(feature)">
                <div class="ic-icon">
                  <span v-bind:class="feature.icon" v-bind:style="colors.menuIcon"></span>
                </div>
                <span class="ic-title">{{ feature.title }}</span></li>
                <v-expansion-panel v-else>
                  <v-expansion-panel-content>
                    <li class="ic-link no-border" slot="header">
                      <div class="ic-icon">
                        <span v-bind:class="feature.icon" v-bind:style="colors.menuIcon"></span>
                      </div>
                      <span class="ic-title">{{ feature.title }}</span></li>
                    <v-card>
                      <ul class="ic-list-link">
                        <li class="ic-link" v-for="children in feature.childrens" v-on:click="goTo(children)">
                          <div class="ic-icon">
                            <span :class="children.icon" v-bind:style="colors.menuIcon"></span>
                          </div>
                          <span class="ic-title">{{ children.title }}</span>
                        </li>
                      </ul>
                    </v-card>
                  </v-expansion-panel-content>
                </v-expansion-panel>
            </div>
          </template>
        </ul>
      </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  computed: {
    ...mapGetters('user', ['user', 'colors', 'transitionSindicoUnit']),
    ...mapGetters('features', ['features', 'area'])
  },
  filters: {
    shortName (value) {
      if (value !== null && value !== '' && value !== undefined) {
        let name = value.split(' ')
        return name.length > 1 ? name[0] + ' ' + name[1] : name[0]
      }
    }
  },
  methods: {
    ...mapActions('shared', ['changeArea']),
    ...mapActions('features', ['goToAnalitics']),
    closeMenu () {
      this.$router.push('/')
    },
    goTo (value) {
      if (value.href) {
        this.goToAnalitics(value)
        // window.location.href = value.url
      } else {
        if (this.area.url === value.url) {
          this.changeArea(this.area)
          this.$router.push('/')
        } else {
          this.$router.push(value.url)
        }
      }
    }
  }
}
</script>

<style lang="css">
.ic-list-wrapper{
  margin: 0;
  padding: 0;
}
.ic-menu{
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  right: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  box-sizing: border-box;
  float: left;
}
.ic-header-fixed{
  position: fixed;
  background: #f4f4f4;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.ic-header-fixed>.ic-bar{
  width: 100%;
  height: 2px;
  position: absolute;
}
.ic-menu>.ic-header-fixed>.ic-menu-header{
  float: left;
  width: 100%;
  box-sizing: border-box;
}
.ic-menu-header>.ic-photo{
  height: 68px;
  width: 68px;
  border-radius: 100%;
  border: 1px solid #e0e0e0;
  padding: 2px;
  box-sizing: border-box;
  float: left;
}
.ic-menu-header>.ic-photo>img{
  height: 62px;
  width: 62px;
  border-radius: 100%;
}
.ic-menu-header>.ic-icon{
  float: left;
  width: 40px;
  box-sizing: border-box;
  padding: 12px;
  font-size: 24px;
}
.ic-menu-header>.ic-user-info{
  float: left;
  width: calc(100% - 40px - 70px);
  padding: 10px;
  box-sizing: border-box;
}
.ic-menu-header>.ic-user-info>.ic-hello{
  white-space: nowrap;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 24px;
}
.ic-menu-header>.ic-user-info>.ic-unit{
  color:#898989;
  min-height: 20px;
}
._p-15{
  padding: 15px;
}
.ic-edit-info>.ic-icon{
  width: 25px;
  float: left;
  font-size: 22px;
  margin-right: 15px;
  margin-left: 10px;
}
.ic-edit-info{
  margin-top: 10px;
  float: left;
  color: #636363;
  font-weight: 300;
  font-size: 18px;
}
.ic-menu-body{
  float: left;
  box-sizing: border-box;
  width: 100%;
  padding-top: 140px;
  height: 100%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background: #fff;
}
.ic-list-link{
  list-style: none;
  margin: 0;
}
.ic-list-link>.ic-link{
  padding: 14px 25px;
  font-size: 18px;
}
.ic-list-link{
  border-bottom: 1px solid #e6e6e6;
}
.ic-list-link:last-child {
  border-bottom: 0px!important;
}
.ic-list-link>.ic-link>.ic-title{
  color: #636363;
  font-weight: 300;
}
.ic-list-link>.ic-link>.ic-icon{
  width: 25px;
  float: left;
  font-size: 23px;
  margin-right: 15px;
}
/* collapse */
.ic-list-link>.expansion-panel{
  box-shadow: none;
}
.expansion-panel__header>.ic-link>.ic-icon{
  width: 25px;
  float: left;
  font-size: 23px;
  margin-right: 15px;
}
.expansion-panel__header>.ic-link>.ic-title{
  color: #636363;
  font-size: 20px;
  font-weight: 300;
}
.expansion-panel__body>.card>.ic-list-link{
  background-color: #f4f4f4;
}
[v-cloak]{display: none;}
.dropdown {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 350ms;
}
.dropdown::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1rem;
  background-image: linear-gradient(to top, white, rgba(255, 255, 255, 0));
}
.dropdown-enter, .dropdown-leave-to {
  opacity: 0;
}
.dropdown-leave, .dropdown-enter-to {
  opacity: 1;
}
.dropdown-enter-active, .dropdown-leave-active {
  position: absolute;
  width: 100%;
  transition: opacity 200ms cubic-bezier(0.4, 0, 0, 1.74);
}
.dropdown-enter-active {
  transition-delay: 100ms;
}
.header__icon i{
  display: none;
}
.header__icon::before {
  content: "\e916";
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}.expansion-panel__container--active>.expansion-panel__header .header__icon{
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}
.transitionSindico,.transitionUnit{
  position: absolute;
  -webkit-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -o-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -ms-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  -moz-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
}
.transitionSindico{
  opacity: 0;
  animation-delay: 5s;
  -webkit-animation-delay: 5s;
}
@-webkit-keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}
@keyframes hide-show {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
}

</style>



