import Vue from 'vue'
import Vuex from 'vuex'
import handler from 'vuex-handler'
import Raven from 'raven-js'

// import home from './home'
import user from './user'
import shared from './shared'
import features from './features'
import booking from './features/reserva'
import loadingScreen from './loadingScreen'

Vue.use(Vuex)

const store = new Vuex.Store({
  plugins: [ handler ],
  modules: {
    user,
    shared,
    features,
    booking,
    loadingScreen
  }
})
const doNothing = {
  onSuccess (result, type, params, options) {
    return result
  },
  onFailure () {
    return
  }
}

store.handler = {
  onSuccess (result, type, params, options) {
    return result
  },
  onFailure (error, type, params, options) {
    console.log(error)
    store.commit('loadingScreen/SET_ERROR', {
      action: type,
      visibility: true
    }, {root: true})
    Raven.captureException(error, { logger: `${type}` })
    return false
  },
  'shared/changeUnit': doNothing,
  'shared/getInfo': doNothing,
  'booking/setDate': doNothing
}

if (module.hot) {
  // accept actions and mutations as hot modules
  module.hot.accept(['./user', './shared', './features', './features/reserva'], () => {
    // require the updated modules
    // have to add .default here due to babel 6 module output
    const newModuleUser = require('./user').default
    const newModuleShared = require('./shared').default
    const newModuleFeatures = require('./features').default
    const newModuleLoadingScreen = require('./loadingScreen').default
    const newModuleBooking = require('./features/reserva').default
    // swap in the new actions and mutations
    store.hotUpdate({
      modules: {
        user: newModuleUser,
        shared: newModuleShared,
        features: newModuleFeatures,
        loadingScreen: newModuleLoadingScreen,
        booking: newModuleBooking
      }
    })
  })
}

export default store



