import moment from 'moment'
export default {
  SET_SPACE_LIST (state, payload) {
    state.spaceList = payload
  },
  SET_BOOKING_DATE (state, payload) {
    state.booking.date = payload
  },
  SET_BOOKING_HOUR (state, payload) {
    if (payload.avaliable) {
      state.booking.hour = payload
      state.hourModal = false
    }
  },
  RESET_BOOKING_DATE (state) {
    state.booking.date = null
  },
  RESET_BOOKING_HOUR (state) {
    state.booking.hour = null
  },
  RESET_HOUR_LIST (state) {
    state.hourList = []
  },
  SET_BOOKING_UNIT (state, payload) {
    state.booking.unit = payload
  },
  SET_HOUR_MODAL (state, payload) {
    if (state.hourList.length === 1) {
      return
    }
    state.hourModal = payload
  },
  SET_HOUR_LIST (state, payload) {
    state.hourList = payload
    if (payload.length === 1 && payload[0].avaliable) {
      state.booking.hour = payload[0]
    }
  },
  SET_DAYS_LIST (state, payload) {
    if (payload.unavaliable) {
      state.space.date.unavaliable = Array.from(new Set(state.space.date.unavaliable.concat(payload.unavaliable)))
    }
    if (payload.partially) {
      state.space.date.partially = Array.from(new Set(state.space.date.partially.concat(payload.partially)))
    }
  },
  SET_SPACE (state, payload) {
    state.space = payload
    if (state.space.antecedence.min === null) {
      state.space.antecedence.min = moment().format('YYYY-MM-DD')
    }
  },
  SET_LOADING_HOUR_LIST (state, payload) {
    state.loadingHourList = payload
  },
  CLEAR_MONTH_YEAR (state, payload) {
    state.monthYear = []
  },
  SET_MONTH_YEAR (state, payload) {
    state.monthYear.push(payload)
  }
}



