<template lang="html">
  <BaseFeatureWrapper title="Avalie nos">
    <h3>Deixe abaixo seu comentario</h3>
    <input type="text" name="" v-model="rating.text" placeholder="Digite aqui...">
    <button type="button" name="button" @click="sendRating">Enviar</button>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import { mapGetters } from 'vuex'
import axios from 'axios'

export default {
  components: {
    BaseFeatureWrapper
  },
  data () {
    return {
      rating: {
        text: ''
      }
    }
  },
  computed: {
    ...mapGetters('user', ['user'])
  },
  methods: {
    sendRating () {
      axios.post('https://cupons-23a86.firebaseio.com/.json', {
        text: this.rating.text,
        user: this.user.name,
        levelLabel: this.user.levelLabel,
        adm: this.user.actualUnit.adm.name,
        condo: this.user.actualUnit.condo,
        unit: this.user.actualUnit.name
      }).then(result => this.clear)
    },
    clear () {
      this.rating.text = ''
    }
  }
}
</script>

<style lang="css">
</style>



