export default {
  features (state) {
    return state.features
  },
  featuresAlert (state) {
    return state.featuresAlert
  },
  mainFeatures (state) {
    const main = ['Mensagens', 'Reservas', 'Notícias', 'Mudanças', 'Enquetes', 'Pessoas']
    return state.features.filter(elem => main.indexOf(elem.title) !== -1)
  },
  area (state) {
    return state.features.find(elem => elem.title === 'Área comum' || elem.title === 'Área privativa') || false
  },
  assistis (state) {
    return false
    // return state.features.find(elem => elem.title === 'Conveniências') || false
  }
}



