<template lang="html">
  <BaseFeatureWrapper title="Selecione a data">
    <v-flex xs12 class="text-xs-center" style="margin-top:10px;">
      <v-date-picker
      class="mt-3"
      :value="booking.date"
      @input="setDate"
      :event-color="eventColor"
      :events="events"
      :min="space.antecedence.min"
      :max="space.antecedence.max"
      :picker-date.sync="pickerDate"
      locale="pt-BR"
      no-title
      :show-current="false"
      ><div v-if="loadingHourList" :class="{'bar-wrapper': true, 'noBar': !loadingHourList}">
  <div class="loading-progress"></div>
</div></v-date-picker>
</v-flex>
    <v-flex xs12 class="" v-if="hasUnavaliableDay || hasPartiallyDay">
      <div class="pickerLabel-wrapper">
        <div class="pickerLabel" v-if="hasUnavaliableDay">
          <div class="bullet red"></div> <p>horarios não disponiveis</p>
        </div>
        <div class="pickerLabel" v-if="hasPartiallyDay">
          <div class="bullet yellow"></div> <p>Alguns horarios não disponiveis</p>
        </div>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center mt-3" v-if="hourList.length > 0">
      <div class="" v-if="booking.hour !== null">
        <h3>
          Periodo escolhido:
        </h3>
        <p>
          {{ booking.hour.description }}: {{ booking.hour.time.start }} até {{ booking.hour.time.end }}
        </p>
        <p v-if="hourList.length > 1" @click="SET_HOUR_MODAL(true)" style="color:#2b68d0">
          escolher outro horario?
        </p>
      </div>

   </v-flex>
   <!-- <v-flex xs12 class="text-xs-center mt-3" v-if="booking.hour !== null">
     <h3>
       Selecione a unidade
     </h3>
     <p>
      0/Lj/00000100
     </p>
  </v-flex> -->
  <v-flex xs12 class="text-xs-center mt-3" v-if="hourList.length === 1 && !hourList[0].avaliable">
    <h3>
      Periodo não disponivel
    </h3>
    <p>
      {{ hourList[0].description }}: {{ hourList[0].time.start }} até {{ hourList[0].time.end }}
    </p>
 </v-flex>
  <v-flex offset-xs1 xs10 v-if="booking.hour !== null">
    <v-btn large block color="green" @click="confirmation">Continuar</v-btn>
 </v-flex>
 <PeriodList/>
  </BaseFeatureWrapper>
</template>

<script>
import BaseFeatureWrapper from '@/components/Feature/BaseFeatureWrapper'
import PeriodList from './PeriodList'
import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
  components: {
    BaseFeatureWrapper,
    PeriodList
  },
  data () {
    return {
      bookingDate: null,
      bookingTime: '',
      antecedence: {
        min: null,
        max: null
      }
    }
  },
  methods: {
    ...mapActions('booking', ['setDate', 'setVisualizationMonth']),
    ...mapMutations('booking', ['SET_BOOKING_DATE', 'SET_BOOKING_UNIT', 'SET_HOUR_MODAL']),
    eventColor (date) {
      return this.space.date.unavaliable.findIndex(elem => elem === date) > -1 ? 'red' : 'yellow'
    },
    confirmation () {
      this.$router.push('/Recurso/Reservas/Nova/Confirmacao')
    }
  },
  computed: {
    ...mapGetters('booking', ['booking', 'hourList', 'space', 'events', 'hasUnavaliableDay', 'hasUnavaliableDay', 'hasPartiallyDay', 'loadingHourList']),
    pickerDate: {
      get () {
        return ''
      },
      set (date) {
        this.setVisualizationMonth(date)
      }
    }
  }
}
</script>

<style lang="css">
.pickerLabel-wrapper{
  width: 262px;
  margin: auto;
  padding: 10px;
  box-sizing: border-box;
}
.pickerLabel{
  position: relative;
  display: block;
  height: 20px;
}
.pickerLabel > p {
  padding-left: 15px;
  margin: 0;
}
.pickerLabel > .bullet {
  border-radius: 50%;
    top: 5px;
    content: "";
    display: block;
    height: 8px;
    position: absolute;
    width: 8px;
}
.bullet.red { background-color: #f44336 !important;}
.bullet.yellow { background-color: #ffeb3b !important}
.btn.green{
  background-color: #4CAF50!important;
  border-color: #4CAF50!important;
  color: white;
}
@keyframes indeterminate-short{
  0%{left:-200%;right:100%}
  60%{left:107%;right:-8%}
  to{left:107%;right:-8%}
}

.bar-wrapper{
  width:100%;
  height: 2px;
  background-color: #517095;
  position: relative;
  overflow:hidden;
  margin: 0px;
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}
.noBar {
  opacity: 0;
}
.loading-progress {
     background-color: #cbd7f5;
     opacity: 0.8;
     height: 10px;
     position:absolute;
     width: 50px;
    -webkit-animation: indeterminate-short;
    animation: indeterminate-short;
    -webkit-animation-duration: 2.2s;
    animation-duration: 2.2s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
.picker__actions.card__actions{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0;
  height: 3px;
}

</style>



